.dropdown {
   width: 300px;
   // border-radius: 6px;
   box-shadow: 0 10px 25px rgba(0, 0, 0, .1);
   background-color: white;
   color: #262470;

   // @media (max-width: 600px) {
   //    width: 159px !important;
   // }

   .dropdown-header {
      padding: 15px;
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   .dropdown-body {
      // padding: 5px;
      border-top: 1px solid #E5E8EC;
      display: none;
   }

   .dropdown-body.open {
      display: block;
      overflow-y: scroll;
      overflow-x: hidden;
      max-height: 400px;
      position: absolute;
      background: #FFF;
      z-index: 1;

      // @media (max-width: 600px) {
      //    width: 159px !important;
      // }
   }

   .dropdown-item {
      padding: 10px;

      &:hover {
         background-color: #91A5BE;
      }

      .selected {
         text-decoration: underline;
         // background-color: #91A5BE;
      }

      &.selected {
         text-decoration: underline;
         background-color: #91A5BE;
      }
   }

   .dropdown-item:hover {
      cursor: pointer;
   }

   .dropdown-item-dot {
      opacity: 0;
      color: #91A5BE;
      transition: all .2s ease-in-out;
   }

   .dropdown-item-dot.selected {
      opacity: 1;
   }

   .icon {
      font-size: 13px;
      color: #91A5BE;
      transform: rotate(0deg);
      transition: all .2s ease-in-out;
   }

   .icon.open {
      transform: rotate(90deg);
   }

}